*{
    font-family: Garamond, serif;
    margin: 0;
}

.search-bar {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    margin: 0;
}

.back-button {
    position: absolute;
    top: 4vh;
    left: 2vw;
}

.container-landing-page-menu {
    margin-top: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.container-landing-page-menu-group {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container-landing-page-menu img {
    border-radius: 50%;
    width: 20vw;
    height: auto;
    cursor: pointer;
    
}

.landing-page-menu-desription {
    margin-top: 3vh;
    font-size: 2em;
    text-align: center;
}

.landing-page-header {
    font-family: Garamond, serif;
    font-size: 4em;
    text-align: center;
    padding-top: 5vh;
}

.landing-page-container {
    width: 100vw;
    height: 100vh;
    background: -webkit-linear-gradient(110deg, #b6b6b6 60%, #d1d1d1 60%);
    background: -o-linear-gradient(110deg, #b6b6b6 60%, #d1d1d1 60%);
    background: -moz-linear-gradient(110deg, #b6b6b6 60%, #d1d1d1 60%);
    background: linear-gradient(110deg, #b6b6b6 60%, #d1d1d1 60%);
  }

  @media only screen and (max-width: 800px) {
    .container-landing-page-menu {
        flex-direction: column;
    }

    .container-landing-page-menu-group {
        margin-bottom: 5vh;
    }

    .container-landing-page-menu img {
        width: 50vw;
    }
  }